@import url("https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;1,400&display=swap");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.10.3/font/bootstrap-icons.css");

body {
  font-family: "Plus Jakarta Sans", sans-serif !important;
  background: none !important;
  user-select: none;
}

.dev-bg {
  width: 100vw;
  height: 100vh;
  background: black;
}

::-webkit-scrollbar {
  width: 15px;
  height: 3px;
}
::-webkit-scrollbar-track {
  background-color: var(--bs-dark-border-subtle);
}
::-webkit-scrollbar-track-piece {
  background-color: var(--bs-dark);
}
::-webkit-scrollbar-thumb {
  height: 50px;
  background-color: var(--bs-dark-border-subtle);
  border-radius: 100px;
  border: 4px solid var(--bs-dark);
}
::-webkit-scrollbar-corner {
  background-color: var(--bs-dark-border-subtle);
}
::-webkit-resizer {
  background-color: var(--bs-dark-border-subtle);
}

.modal-content {
  border-radius: 10px !important;
}

.modal-header {
  border-radius: 10px 10px 0 0 !important;
}

.modal-footer {
  border-radius: 0 0 10px 10px !important;
}

.accordion-button {
  box-shadow: none !important;
}

.accordion-button:not(.collapsed) {
  background-color: var(--bs-tertiary-bg) !important;
  color: white !important;
}

.accordion-item {
  border: none !important;
}

.edit-vehicle-name {
  opacity: 0 !important;
}

.vehicle-acc-header:hover .edit-vehicle-name {
  opacity: 1 !important;
}

.edit-vehicle-name-input {
  border: 1px dashed #999;
  outline: none;
  background-color: transparent;
  color: white;
  padding: 5px 7px;
  border-radius: 5px;
  width: 400px;
}

.search-bar {
  position: sticky;
  top: 0;
  z-index: 99;
  backdrop-filter: blur(10px);
}

.finance-container {
  height: 20px;
  /* border: 1px solid red; */
}

.finance-progress-bar {
  margin: 5px;
  position: relative;
  width: calc(100% - 5px);
  height: 4px;
  background: #555;
}

.finance-progress-bar-inner {
  position: absolute;
  left: 0;
  max-width: 100%;
  height: 4px;
}

.finance-progress-bar .points-container {
  display: flex;
  justify-content: space-between;
}

.finance-progress-bar .point {
  position: relative;
  width: 18px;
  height: 18px;
  background-color: white;
  border-radius: 100%;
  margin-top: -6px;
  margin-left: -3px;
  z-index: 1;
}

.finance-progress-bar .point i {
  position: absolute;
  top: -1px;
  left: -1px;
  line-height: 0;
  font-size: 20px;
  color: #555;
}

.modal-backdrop.show {
  z-index: 9999 !important;
}

.garage-row-image {
  width: 70px;
  height: 50px;
  margin-right: 10px;
  background-size: contain;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
